import { mapState } from 'vuex'
export default {
    name: 'city',
    data() {
        return {
            cityArr: [{
                id:1,
                name:'北京城区'
            },{
                id:2,
                name:'北京城区'
            },{
                id:3,
                name:'北京城区'
            },{
                id:4,
                name:'北京城区'
            },{
                id:5,
                name:'北京城区'
            },{
                id:6,
                name:'北京城区'
            }]
        }
    },
    computed: {
        ...mapState(['locationObj'])
    },
    created() {
        this.getCityList()
    },
    methods: {
        choseCity (obj) {
            this.$store.commit('SET_LOCATION_OBJ',obj)            
            uni.redirectTo({
                url: '/pages/main/main'
            })
        },
        async getCityList () {
            let params = {
                pageSize: 100
            }
            const res = await this.$api.getPlaceList(params)
            if (res.code === 0) {
                this.cityArr = res.data.data
            }
        }
    },
}
